<template>
  <div id="app">
    <div class="background">
      <MainMenu />
        <router-view />
        <vue-progress-bar />
      <MainFooter />
      <parallax-container class="parallax-container">
        <parallax-element class="parallax-item item1" :parallaxStrength="-2" :type="'translation'" />
        <parallax-element class="parallax-item item2" :parallaxStrength="-2" :type="'translation'" />
        <parallax-element class="parallax-item item3" :parallaxStrength="-2" :type="'translation'" />
        <parallax-element class="parallax-item item4" :parallaxStrength="-2" :type="'translation'" />
        <parallax-element class="parallax-item item5" :parallaxStrength="-5" :type="'translation'" />
        <parallax-element class="parallax-item item6" :parallaxStrength="-5" :type="'translation'" />
        <parallax-element class="parallax-item item7" :parallaxStrength="-5" :type="'translation'" />
        <parallax-element class="parallax-item item8" :parallaxStrength="-5" :type="'translation'" />
      </parallax-container>
    </div>
  </div>
</template>

<script>
import './assets/bootstrap-reboot.css';
import './assets/bootstrap-grid.css';
import './assets/Fonts.css';
import './assets/buttons.css';

import MainMenu from './components/MainMenu';
import MainFooter from './components/MainFooter';
export default {
  name: 'App',
  components: {
    MainMenu,
    MainFooter,
  },
};
</script>

<style>
* {
  box-sizing: border-box;
}

a {
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
  text-decoration: none;
}

#app {
  font-family: 'Circe', sans-serif;
  min-height: 100vh;
  padding: 20px 20px 0;
  position: relative;
  background-color: #f7f7f7;
  display: flex;
  overflow: hidden;
}

.parallax-container {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 0;
}

.parallax-item {
  position: absolute;
  background-repeat: no-repeat;
  z-index: 0;
}

.parallax-item.item1 {
  background-image: url('./assets/img/parallax/1.svg');
  width: 46px;
  height: 41px;
  top: 6px;
  left: 55%;
}

.parallax-item.item2 {
  background-image: url('./assets/img/parallax/2.svg');
  width: 72px;
  height: 72px;
  top: 20%;
  left: -36px;
}

.parallax-item.item3 {
  background-image: url('./assets/img/parallax/3.svg');
  width: 40px;
  height: 40px;
  top: 20%;
  left: 17%;
}

.parallax-item.item4 {
  background-image: url('./assets/img/parallax/4.svg');
  width: 34px;
  height: 34px;
  top: 25%;
  right: 25%;
}

.parallax-item.item5 {
  background-image: url('./assets/img/parallax/5.svg');
  width: 110px;
  height: 106px;
  bottom: 25%;
  left: 28%;
}

.parallax-item.item6 {
  background-image: url('./assets/img/parallax/6.svg');
  width: 170px;
  height: 182px;
  top: 55%;
  right: 20%;
}

.parallax-item.item7 {
  background-image: url('./assets/img/parallax/7.svg');
  width: 145px;
  height: 143px;
  top: 40%;
  left: 20%;
}

.parallax-item.item8 {
  background-image: url('./assets/img/parallax/8.svg');
  width: 180px;
  height: 107px;
  top: 30%;
  right: -90px;
}

.background {
  background-color: #fff;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  width: 100%;
  position: relative;
}

.background > div {
  width: 100%;
}

.v--modal-overlay .v--modal-background-click {
  padding: 105px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.v--modal-box {
  position: static !important;
  overflow: visible !important;
  height: auto !important;
}

.common-button,
.common-button-white {
  background-color: #fff;
  padding: 20px 51px;
  border-radius: 32px;
  outline: 0;
  border: 0;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  position: relative;
  z-index: 1;
  display: inline-block;
}

.common-button:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 32px;
  background: #425fff;
  z-index: -1;
  opacity: 0;
  transform: scale3d(0.6, 0.6, 1);
  transition: transform 0.3s, opacity 0.3s;
  transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}

.common-button {
  background-image: linear-gradient(351deg, #274bff, #081cff);
  color: #fff;
  transition: all 0.3s ease-in-out;
}

.common-button:hover:before {
  transform: scale3d(1, 1, 1);
  opacity: 1;
}

.common-button-white {
  background-image: white;
  color: rgba(0, 0, 0, 0.2);
  border: solid 1px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease-in-out;
}

.common-button-white:hover {
  background-image: linear-gradient(351deg, #274bff, #081cff);
  color: #fff;
}

.common-button:hover {
  color: #fff;
  text-decoration: none;
}

.sub-title {
  font-size: 20px;
  font-weight: 300;
  letter-spacing: 0.5px;
  color: #6e6e6e;
  z-index: 1;
}

.title {
  font-size: 65px;
  font-weight: 300;
  letter-spacing: 0.7px;
  line-height: 60px;
  margin-bottom: 10px;
  z-index: 1;
}
</style>



